
import { factor } from './pkg/wasm_worker_test.js';

self.postMessage(true);
self.addEventListener('message', (ev) => {
    console.log('Factoring: ', ev.data);
    const result = factor(String(ev.data));
    console.log('Factors: ', result);
    const factors = result.split(',').map((s) => BigInt(s));
    self.postMessage(factors);
});
